.stars {
  display: flex;

  &__star {
    width: 16px;
    height: 16px;
    background-image: url(../images/star.svg);
    background-repeat: no-repeat;
    background-position: center;
  }

  @for $i from 1 through 5 {
    &--#{$i} > :nth-child(-n + #{$i}) {
      background-image: url(../images/star-active.svg);
    }
  }

  &__star:nth-child(n+2) {
    margin-left: $gap;
  }
}
